/* slide styles */
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.slide {
  color: #ddd;
  width: 100%;
  height: 90vh;
  overflow: hidden;
  position: relative;
  z-index: 1;
  background-size: cover;
}
.slide-show {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 300vw;
  height: 100%;
  overflow: hidden;
  position: absolute;
  -webkit-animation: slideshow 30s ease-in infinite;
          animation: slideshow 30s ease-in infinite;
}
.slide-item {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0;
  padding-top: 90px;
  position: relative;
}
.slide-item img {
    width: 100%;
    height: 100%;
}
.slide-text {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0 50px;
  height: 100%;
  background: rgba(34, 34, 34, 0.6);
  padding-bottom: 10px;
  font-size: 30px;
}
.slide-text .slide-title {
    color: #348AFE;
    -webkit-animation: slide-title-in 10s linear infinite;
            animation: slide-title-in 10s linear infinite;
    opacity: 0;
    margin-bottom: 7px;
}
.slide-text div {
    width: 100%;
    text-align: center;
}
.slide-text div:nth-child(1) {
    padding: 50px 0;
}
.slide-text .button-container {
    margin: 25px 0;
}
.slide-indicator {
  width: 200px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
  position: absolute;
  bottom: 5%;
  left: 100px;
}
.slide-indicator .indicator-item {
    height: 3px;
    width: 100%;
    margin: 0 5px;
    background: rgba(52, 138, 254, 0.3);
    list-style: none;
}
[rol="low-indicator"] {
  display: block;
  height: 100%;
  width: 0;
  background: #348AFE;
}
.indicator-one {
  -webkit-animation: indicator-one 30s linear infinite;
          animation: indicator-one 30s linear infinite;
}
.indicator-two {
  -webkit-animation: indicator-two 30s linear infinite;
          animation: indicator-two 30s linear infinite;
}
.indicator-three {
  -webkit-animation: indicator-three 30s linear infinite;
          animation: indicator-three 30s linear infinite;
}
.indicator-four {
  -webkit-animation: indicator4;
          animation: indicator4;
}
.blur-slide {
  -webkit-filter: blur(3px);
          filter: blur(3px);
}
@-webkit-keyframes slideshow {
0% {
    left: 0;
}
33% {
    left: 0;
}
36% {
    left: -100vw;
}
63% {
    left: -100vw;
}
66% {
    left: -200vw;
}
96% {
    left: -200vw;
}
100% {
    left: 0;
}
}
@keyframes slideshow {
0% {
    left: 0;
}
33% {
    left: 0;
}
36% {
    left: -100vw;
}
63% {
    left: -100vw;
}
66% {
    left: -200vw;
}
96% {
    left: -200vw;
}
100% {
    left: 0;
}
}
@-webkit-keyframes indicator-one {
0% {
    width: 0;
}
35% {
    width: 100%;
}
99% {
    width: 100%;
}
}
@keyframes indicator-one {
0% {
    width: 0;
}
35% {
    width: 100%;
}
99% {
    width: 100%;
}
}
@-webkit-keyframes indicator-two {
0% {
    width: 0;
}
36% {
    width: 0;
}
65% {
    width: 100%;
}
99% {
    width: 100%;
}
}
@keyframes indicator-two {
0% {
    width: 0;
}
36% {
    width: 0;
}
65% {
    width: 100%;
}
99% {
    width: 100%;
}
}
@-webkit-keyframes indicator-three {
0% {
    width: 0;
}
66% {
    width: 0;
}
99% {
    width: 100%;
}
}
@keyframes indicator-three {
0% {
    width: 0;
}
66% {
    width: 0;
}
99% {
    width: 100%;
}
}
@-webkit-keyframes slide-title-in {
3% {
    opacity: 0;
}
25% {
    opacity: 1;
}
90% {
    opacity: 1;
}
100% {
    opacity: 0;
}
}
@keyframes slide-title-in {
3% {
    opacity: 0;
}
25% {
    opacity: 1;
}
90% {
    opacity: 1;
}
100% {
    opacity: 0;
}
}
.btn,
.call-to-action {
  text-align: center;
  padding: 0 15px;
  background: -webkit-gradient(linear, left bottom, left top, from(#48c6ef), to(#6f86d6));
  background: linear-gradient(to top, #48c6ef 0%, #6f86d6 100%);
  border: 1px solid rgba(52, 138, 254, 0.6);
  height: 48px;
  font-size: 16px;
  text-decoration: none;
  color: #fff;
  padding: 10px 20px;
  border-radius: 3px;
}
.btn:hover,
  .call-to-action:hover {
    background: -webkit-gradient(linear, left bottom, left top, from(#48c6ef), to(#6f86d6));
    background: linear-gradient(to top, #48c6ef 0%, #6f86d6 100%);
}

/* smartphone */
@media (max-width: 768px) {
.slide {
    width: 100%;
    height: 80vh;
    overflow: hidden;
    position: relative;
    z-index: 1;
    min-height: 528px;
}
.slide .indicator-item {
      height: 2px;
}
.slide small {
      font-size: 16px;
}
.slide-indicator {
    width: 80%;
    left: 10%;
}
.slide-item {
    padding-top: 60px;
}
.slide-title {
    font-size: 20px;
    margin-bottom: 20px;
}
.page-info .brand-description p {
    text-align: left;
}
.slide-text {
    padding: 0 10px 20px 10px;
}
}
.slide {
  background-image: url("./../assets/img/ipad-tablet.jpg");
}



