
.header-container {
  position: fixed;
  width: 100%;
  z-index: 100;
}
.top-header {
  height: 20px;
  background: whitesmoke;
  padding: 0 50px;
  text-align: right;
  border-bottom: 1px sold #ccc;
  display: none;
}
.responsive-img {
  height: 100%;
  margin-right: 15px;
}
header.row {
  width: 100%;
  margin: 0;
  padding: 15px 50px;
  background: #fff;
  height: 90px;
  -webkit-box-shadow: 1px 2px 2px rgba(0, 0, 0, 0.2);
          box-shadow: 1px 2px 2px rgba(0, 0, 0, 0.2);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-line-pack: center;
      align-content: center;
  position: relative;
  border-bottom: 1px solid #ccc;
}
header.row:after {
    content: '';
    height: 7px;
    background: #348AFE;
    width: 10%;
    position: absolute;
    bottom: -7px;
    right: 0;
    -webkit-box-shadow: 1px 2px 2px transparentze(#000, 0.8);
            box-shadow: 1px 2px 2px transparentze(#000, 0.8);
    border-radius: 0 0 0 6px;
}
.brand-name {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 100%;
  cursor: pointer;
}
.brand-name .logo-box {
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.brand-name .logo-box .name {
      margin-bottom: 0;
      width: 100%;
      color: #348AFE;
}
.brand-name .logo-box .statement {
      color: #FE6534;
}
.brand-name a {
    color: #348AFE;
    text-decoration: none;
    -webkit-transition: all ease .5s;
    transition: all ease .5s;
}
.brand-name a:hover {
      color: #9ecfff;
}
.nav-container,
.hamburger-menu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  justify-self: flex-end;
  overflow: hidden;
  -ms-flex-item-align: end;
      align-self: flex-end;
  margin-left: auto;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.hamburger-menu {
  display: none;
}
.main-nav {
  display: table;
  color: #348AFE;
  list-style: none;
  height: 100%;
}
.main-nav li {
    display: table-cell;
    padding: 0 8px;
}
.nav-button {
  color: #3e5871;
  display: block;
  padding-top: 5px;
  padding-bottom: 5px;
  font-size: 16px;
  font-weight: bolder;
  text-decoration: none;
  -webkit-transition: all ease .5s;
  transition: all ease .5s;
  position: relative;
}
.nav-button:after {
    content: '';
    position: absolute;
    height: 3px;
    width: 0;
    bottom: 3px;
    left: 0;
    background: #FE6534;
    border-radius: 3px;
    -webkit-transition: all ease .5s;
    transition: all ease .5s;
}
.nav-button:hover {
    text-decoration: none;
    color: #FE6534;
}
.nav-button:hover:after {
      width: 100%;
}
.special {
  background: -webkit-gradient(linear, left bottom, left top, from(#48c6ef), to(#6f86d6));
  background: linear-gradient(to top, #48c6ef 0%, #6f86d6 100%);
  color: #fff;
  width: 150px;
  border-radius: 3px;
  text-align: center;
  position: relative;
}
.special:after {
    content: '';
    position: absolute;
    height: 100%;
    width: 0;
    top: 0;
    left: 0;
    background: rgba(255, 255, 255, 0.13);
    border-radius: 3px;
    -webkit-transition: all ease .2s;
    transition: all ease .2s;
}
.special:hover {
    color: #fff;
}
.special:hover:after {
      width: 100%;
}
@media (max-width: 960px) {
.top-header {
    display: none;
}
header.row {
    -webkit-box-pack: left;
        -ms-flex-pack: left;
            justify-content: left;
    height: 70px;
    padding-left: 7%;
    padding-right: 7%;
}
.responsive-img {
    margin-right: 7px;
}
.nav-container {
    display: none;
}
.nav-container.expanded {
      display: block;
      position: absolute;
      width: 100%;
      height: 100vh;
      top: 70px;
      right: 0;
      z-index: 101;
      background: white;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      -ms-flex-line-pack: center;
          align-content: center;
}
.nav-container.expanded .main-nav {
        display: block;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
        margin-top: 10%;
        width: 100%;
}
.nav-container.expanded .main-nav li {
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          padding: 2% 20%;
          text-align: center;
          -webkit-box-pack: center;
              -ms-flex-pack: center;
                  justify-content: center;
}
.nav-container.expanded .nav-button {
        font-size: 22px;
}
.hamburger-menu {
    display: block;
    font-size: 20px;
    font-weight: 900;
}
.name {
    font-size: 18px;
}
.statement {
    font-size: 7px;
}
}



